import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
            list: [],
			DrId: this.$store.state.userInfo == null ? 18 : this.$store.state.userInfo.Data.User.DoctorId,
            pageShow: false,
			page:1,
			haveMore: true,
			totalNum:0,
			search: '',
        }
    },
    created(){
		this.getReferralRecord(false)
    },
    mounted(){
		 this.getMore()
    },
    beforeDestroy(){
		window.onscroll = null
    },
    methods:{
		getReferralRecord(h){
			if(this.haveMore){
				this.$post('PlatFormAPI/DoctorReferral/QueryPatientReferralSetPage',{
				    DrId: this.DrId,
					PageIndex: this.page,
					pageSize: 10,
					KeyName:this.search
				}).then(o => {
					this.pageShow = true
					if (!o.ReturnData || o.ReturnData.length < 10){
					    this.haveMore = false
					}
					else {
					    this.haveMore = true
					}
					
					if (h){
					    o.ReturnData.forEach(v => {
					        this.list.push(v)
					    })
					}
					else {
					    this.list = o.ReturnData
					}
					this.totalNum = o.Total
				})
			}
		},
		getMore(){
		    const el = this.$refs.el
		    let timer = null
		    window.onscroll = () => {
		        clearTimeout(timer)
		        timer = setTimeout(() => {
		            const top = document.documentElement.scrollTop
		                        || window.pageYOffset
		                        || document.body.scrollTop,
		                    h = el.scrollHeight,
		                    wh = window.innerHeight
		
		            if (h - top - wh < 100 && this.haveMore){
		                this.page += 1
		                this.getReferralRecord(true)
		            }
		        }, 100)
		    }
		},
		 searchFor(){
		 	if(this.inputFlag) {
				return
			}
		    this.haveMore = true
		    this.page = 1
		    this.getReferralRecord(false)
		},
		keepInfo(i){
			this.$store.dispatch('pushpatient', i)
			this.$post('IMAPI/Message/GetNullIMRelationRecord',{
                DoctorId: this.DrId,
                PatientId: i.PatientId
            }).then(o => {
				o.Total = this.Total
				o.PatientInfo = {
					PatientId: i.PatientId,
					PatientName: i.PatientName,
					PatientSex: i.PatientSex,
					Birthday: i.Birthday,
					Age: i.Age,
				},
				o.DoctorPatientRelation = {
					DrPatientId: i.DrPatientId || 0,
				}
                this.$bridge.callhandler('SendMessage', JSON.stringify(o))
            })
			
			//this.$router.push(`/archives`)
		}
    }
}



